* {
  margin: 0;
  padding: 0;
}

body {
  max-width: 1600px;
  margin: auto;
}

header {
  width: 100%;
  height: 80vh;
  background-image: url("/img/header-background.jpg");
  background-position: center;
  position: relative;
}

header h1 {
  font-family: 'Damion', cursive;
  height: 140px;
  width: 100%;
  color: white;
  line-height: 140px;
  text-align: center;
  font-size: 6em;
  position: absolute;
  top: 50vh;
  margin-top: -150px;
  animation: fadeIn 0.5s ease-in;
  opacity: 0;
  animation-fill-mode: forwards;
  animation-delay: 0.5s;
  background-color: rgba(208, 0, 60, 0.4);
}

header h1::before {
  content: "";
  background-color: white;
  height: 3px;
  width: 100%;
  left: 0;
  position: absolute;
  animation: rollOutLeft 0.8s linear;
  animation-delay: 0.5s;
}

header h1::after {
  content: "";
  background-color: white;
  height: 3px;
  width: 100%;
  right: 0;
  bottom: 0;
  position: absolute;
  animation: rollOutRight 0.8s linear;
  animation-delay: 0.5s;
}

@media (max-width: 800px) and (orientation: landscape) {
  header h1 {
    top: 55vh;
  }
}

@media (min-width: 800px) {
  header {
    background-size: auto;
    background-attachment: fixed;
    background-position: center;
  }
  header h1 {
    width: 500px;
    left: 50%;
    margin-left: -250px;
  }
}

.down_arrow {
  width: 100px;
  height: 100px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  margin-left: -50px;
  opacity: 0;
  animation: moveDown 1.3s ease-in-out 1.5s infinite;
}

.down_arrow span {
  position: absolute;
  height: 50px;
  width: 3px;
  padding: 3px;
  background-color: white;
  border-radius: 10px;
}

.down_arrow span:nth-of-type(1) {
  transform: rotate(50deg);
  right: 27px;
}

.down_arrow span:nth-of-type(2) {
  transform: rotate(-50deg);
  left: 27px;
}

@keyframes fadeIn {
  100% {
    opacity: 1;
  }
}

@keyframes rollOutLeft {
  0% {
    width: 0;
  }
}

@keyframes rollOutRight {
  0% {
    width: 0;
  }
}

@keyframes moveDown {
  from {
    opacity: 0;
    bottom: 50px;
  }
  90% {
    opacity: 1;
    bottom: 5px;
  }
  to {
    opacity: 0;
    bottom: 5px;
  }
}

.section-first {
  height: 2000px;
  background-color: white;
  position: relative;
}

.section-first::before {
  position: absolute;
  content: "";
  display: inline-block;
  height: 1vh;
  width: 100vw;
  background-color: white;
  top: -2vh;
}
